<template>
	<div class="row" style="padding: 8px 16px">
		<div :class="'col-' + (setting.columns || 2)" v-for="setting in settings" :key="setting.id">
			<inputs v-bind="setting" @onChange="onChange" @onSelect="onChange" :value="getValue(setting)" />
		</div>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

import Inputs from 'src/components/inputs'

export default defineComponent({
	'name': 'SettingPage',
	'props': [ 'settings' ],
	
	'components': {
		Inputs
	},
	
	'methods': {
		onChange({ id, value }) {
			this.$store.commit('editor/set', { ['settings.' + id]: value });
		},
		getValue(setting) {
			return this.$store.state.editor.settings[setting.id] !== undefined ? this.$store.state.editor.settings[setting.id] : setting.value;
		}
	}
});
</script>
