<template>
	<div class="full-height">
		<div class="row no-wrap">
			<q-toolbar class="paper" style="min-height: 56px">
				<q-toolbar-title shrink>{{ $t('Settings') }}</q-toolbar-title>
			</q-toolbar>
		</div>
		
		<div v-show="!expertMode" class="row no-wrap paper" v-for="group in groups" :key="group.id">
			<q-expansion-item
				v-model="expanded[group.id]"
				:icon="group.icon"
				:label="$t(group.label)"
				class="full-width"
				>
				
				<setting-page :settings="group.settings" />
			</q-expansion-item>
		</div>
		
		<paper v-show="expertMode" :style="{ 'height': (pageHeight-66-16) + 'px' }">
			<json-editor id="settings" :json="json" @onChange="onJsonEditor" />
		</paper>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import SettingPage from './SettingPage.vue';
import Config from './SettingsPage.config';

import JsonEditor from 'src/components/json-editor.vue';

import i18n from './i18n';
export default defineComponent({
	'name': 'SettingsPage',
	
	'components': {
		JsonEditor,
		SettingPage
	},
	
	data() {
		return {
			'expanded': {},
			'groups': Config
		}
	},
	
	'methods': {
		onChange({ id, error, value }) {
			//
			if (error) {
				this.$store.commit('editor/addError', { 'key': 'settings-' + id, 'val': error.message || error });
			}
			
			//
			else if (value) {
				this.$store.commit('editor/removeError', { 'key': 'settings-' + id });
				
				for (const key in value) {
					this.$store.commit('editor/set', { ['settings.' + key]: value[key] });
				}
			}
		},
		onJsonEditor(props) {
			if (!props._error) {
				this.$store.commit('editor/set', { [props.id]: JSON.parse(props.value) });
			}
		}
	},
	
	'computed': {
		pageHeight() {
			return this.$store.state.jarvis.pageHeight;
		},
		json() {
			return this.$store.state.editor.settings;
		},
		expertMode() {
			return this.$store.state.editor.settings?.configExpertMode;
		}
	},
	
	created() {
		i18n.register(this.$i18n, i18n.translations);
	}
});
</script>
